import React from "react";
import Lottie from "react-lottie";
import animationData from "../assets/lottie/Balun.json";
import { FormattedMessage } from "react-intl";

const Home = () => {
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: animationData,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  return (
    <div className="main flex flex-column pHorizontal ">
      <div className="relative">
        <img
          className="logoHeader absolute h-6rem imgSphere"
          src={require("../assets/img/mediaSphere.png")}
          alt="Logo"
        />

        <div className="boxLottie absolute z-0">
          <Lottie options={defaultOptions} width={"100%"} height={"100%"} />
        </div>
        <div className="h-screen flex justify-content-center flex-column">
          <div className="z-1 text-white boxTitle text-4xl font-semibold">
            Es hora de expandir su empresa <br />
            mediante las nuevas tecnologías,
            {/* <FormattedMessage id="app.welcome" defaultMessage="Welcome" /> */}
          </div>
          <div className="z-1 text-white-alpha-70 boxTitle text-2xl mt-5 font-normal">
            Como la de la cadena de bloques, web 3.0, Fintech, <br />
            Metaverso y criptomonedas.
          </div>
          <div className="z-1 flex flec-row mt-6">
            <button className="buttonContact border-none mr-4 cursor-pointer">
              <span className="buttonContact-content text-base font-semibold">
                ¡Contratanos!
              </span>
            </button>
            <button className="buttonLearn cursor-pointer border-none">
              <span className="text-base font-semibold">Aprende más</span>
            </button>
          </div>
        </div>

       
      </div>
      <div className="relative">
        <div className="z-1 bg-primary mt-1 h-screen">
          <h1 className="text-white ">
            Es hora de expandir su empresa mediante las nuevas tecnologías,
            {/* <FormattedMessage id="app.welcome" defaultMessage="Welcome" /> */}
          </h1>
        </div>
      </div>
    </div>
  );
};

export default Home;
