import React, { useRef, useContext } from "react";
import { SlideMenu } from "primereact/slidemenu";
import { Button } from "primereact/button";
import {FormattedMessage} from "react-intl"
import { langContext } from "../context/langContext";

const Header = () => {
  const idioma = useContext(langContext);

  const menu = useRef(null);

  const items = [
    {
      label: "Nuestra Empresa",
    },
    {
      label: "Nuestros Servicios",
    },
    {
      label: "Contacto",
    },
    {
      label: "Tecnologías",
    },
    {
      label: "Launch App",
      icon:'pi pi-arrow-up-right',
    },
    
  ];

  return (
    <header className="z-1">
      <div className="flex flex-row pHorizontal lala pt-3 pb-3">
        <div className="col-10 md:col-3 lg:col-4 font-bold text-white flex align-items-center boxLogo lg:justify-content-start">
          <img
            className="logoHeader boxLogoImg  md:w-8 lg:w-6 xl:w-4 h-3rem"
            src={require("../assets/img/logoHeader.png")}
            alt="Logo"
          />
        </div>
        <div className="col-2 md:col-9 lg:col-8 font-bold text-white flex justify-content-end md:justify-content-end md:justify-content-around align-items-center nav lg:px-5 text-sm">
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage id="menu.company" defaultMessage="Our Company" />
          </div>
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage
              id="menu.services"
              defaultMessage="Our Services"
            />
          </div>
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage id="menu.contact" defaultMessage="Contact" />
          </div>
          <div className="font-light cursor-pointer hidden md:block mx-2">
            <FormattedMessage
              id="menu.technologies"
              defaultMessage="Technologies"
            />
          </div>

          <button onClick={() => idioma.establecerLenguaje("es-ES")} className="button-85 hidden md:block ml-2" role="button ">
            <FormattedMessage
              id="menu.launchApp"
              defaultMessage="Launch App"
            />
          </button>

          {/* <button onClick={() => idioma.establecerLenguaje("en-US")} className="button-85 hidden md:block ml-2" role="button ">
            <FormattedMessage
              id="menu.launchApp"
              defaultMessage="Launch App"
            />
          </button> */}

          <div className="block md:hidden">
            <SlideMenu
              ref={menu}
              model={items}
              popup
              viewportHeight={220}
              menuWidth={175}
            ></SlideMenu>
            <Button
              type="button"
              icon="pi pi-bars"
              onClick={(event) => menu.current.toggle(event)}
            ></Button>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header;
